import React from "react";
import { Card, CardContent, Typography } from "@material-ui/core";
import "./InfoBox.css";

function InfoBox({ title, cases, total, className, ...props }) {
  return (
    <Card
      onClick={props.onClick}
      className={`c19t-infoBox ${className}`}
    >
      <CardContent>
        <Typography color="textSecondary" gutterBottom>
          {title}
        </Typography>
        <h2 className={`c19t-infoBox__cases`}>
          {cases}
        </h2>

        <Typography className="c19t-infoBox__total" color="textSecondary">
          {total} Total
        </Typography>
      </CardContent>
    </Card>
  );
}

export default InfoBox;
